export class Audio {
  constructor() {
    this.foregroundSounds = []
    this.backgroundSounds = []

    const contextClass = (
      window.AudioContext
      || window.webkitAudioContext
      || window.mozAudioContext
      || window.oAudioContext
      || window.msAudioContext)

    if (contextClass) {
      this.context = new contextClass()
    } else {
      console.log('No audio context')
    }

    this.backgroundGainNode = this.context.createGain()
    this.foregroundGainNode = this.context.createGain()

    this.levels = {
      background: 0.20,
      foreground: 0.65,
      fade: 0.10
    }

    this.prePauseLevels = {
      background: 0.20,
      foreground: 0.65,
      fade: 0.10
    }

    this.applyLevels()
  }

  playSound = (filePath, background = false, loop = false) => {
    this.play(filePath, background, loop)
  }

  stopSound = (filePath) => {
    let index = this.findFileIndex(this.foregroundSounds, filePath)
    this.stopEntry(this.foregroundSounds, index)

    if (index === -1) {
      index = this.findFileIndex(this.backgroundSounds, filePath)
      this.stopEntry(this.backgroundSounds, index)
    }
  }

  play = (filePath, background, loop) => {
    const request = new XMLHttpRequest()
    request.open('GET', '/Audio/'+filePath, true)
    request.responseType = 'arraybuffer'

    const closeover = this

    // Decode asynchronously and connect the sound from source to
    // appropriate sink
    request.onload = function() {
      closeover.context.decodeAudioData(request.response,
        function(theBuffer) {
          const source = closeover.context.createBufferSource()
          source.buffer = theBuffer
          source.loop = loop

          const list = (background)
            ? closeover.backgroundSounds : closeover.foregroundSounds
          const gainNode = (background)
            ? closeover.backgroundGainNode
            : closeover.foregroundGainNode

          list.push({ filePath: filePath, source: source })

          // re evaluate levels after sound ends
          source.onended = function() {
            closeover.stopSource(list, source)
            closeover.backgroundGainNode.gain.value
              = (closeover.foregroundSounds.length > 0)
              ? closeover.levels.fade : closeover.levels.background
          }

          source.connect(gainNode)
          gainNode.connect(closeover.context.destination)

          closeover.backgroundGainNode.gain.value
            = (closeover.foregroundSounds.length > 0)
            ? closeover.levels.fade : closeover.levels.background
          source.start(0)
        }, this.onError)
    }

    request.send()
  }

  configureSound = (soundVolume, backgroundSoundVolume,  fadeVolume) => {
    this.levels = {
      background: backgroundSoundVolume,
      foreground: soundVolume,
      fade: fadeVolume
    }

    this.applyLevels()
  }

  applyLevels = () => {
    this.backgroundGainNode.gain.value = this.levels.background
    this.foregroundGainNode.gain.value = this.levels.foreground
  }


  mute = () => {
    this.prePauseLevels = { ...this.levels }
    this.levels = { background:0, foreground:0, fade:0 }
    this.applyLevels()
  }

  unmute = () => {
    this.levels = this.prePauseLevels
    this.applyLevels()
  }

  // safari on ios requires the initial sound to be caused by user interaction
  // so this method should be a callback for a user initialted event
  allowSound = () => {
    const oscillator = this.context.createOscillator()
    const analyser = this.context.createAnalyser()

    // Setup the graph.
    const oscillatorGainNode = this.context.createGain()
    oscillatorGainNode.gain.value = 0.0
    oscillator.connect(analyser)
    analyser.connect(oscillatorGainNode)
    oscillatorGainNode.connect(this.context.destination)

    oscillator[oscillator.start ? 'start' : 'noteOn'](0)

    setTimeout(function() {
      oscillator.stop(0)
    }, 1)
  }

  onError(err) {
    console.log('err: '+err)
  }

  findFileIndex(list, filePath) {
    const index = list.findIndex( (element) => {
      return element.filePath === filePath
    })

    return index
  }

  findFirstSourceIndex(list, source) {
    const index = list.findIndex( (element) => {
      return element.source === source
    })

    return index
  }

  stopSource(list, source) {
    const index = this.findFirstSourceIndex(list, source)
    this.stopEntry(list, index)
  }

  stopEntry(list, index) {
    if (index > -1) {
      const removedEntry = list.splice(index, 1)
      removedEntry[0].source.stop()
    }
  }
}



