// Game Developers - Please Do NOT Edit this file

const { Audio } = require('./audio')
const audio = new Audio()

const LOLSDK = {}

const questionListeners = []
const lifecycleListeners = []

LOLSDK.init = function(applicationId) {
  console.log('init from '+applicationId)
  parent.postMessage({ message: 'init' }, '*')
}

LOLSDK.submitProgress = function(progress) {
  parent.postMessage({
    message:'progress',
    payload: JSON.stringify(progress)
  }, '*')
}

LOLSDK.completeGame = function() {
  parent.postMessage({ message:'complete' }, '*')
}

LOLSDK.addQuestionListener = function(callback) {
  questionListeners.push(callback)
}

LOLSDK.addLifecycleListener = function(pauseCallback, resumeCallback) {
  const listener = {
    pauseCallback: pauseCallback,
    resumeCallback: resumeCallback
  }

  lifecycleListeners.push(listener)
}

LOLSDK.submitAnswer = function(answer) {
  const answerMessage = {
    message:'answer',
    payload: JSON.stringify(answer)
  }
  parent.postMessage(answerMessage, '*')
}

LOLSDK.acknowledge = function(message) {
  parent.postMessage({ message: 'acknowledge', payload: message }, '*')
}

LOLSDK.error = function(message) {
  parent.postMessage({ message: 'error', payload: message }, '*')
}

LOLSDK.sendLoadingProgress = function(loadingProgress) {
  parent.postMessage({ message:'loadingProgress',
    payload: ''+loadingProgress }, '*')
}

LOLSDK.allowSound = audio.allowSound
LOLSDK.playSound = audio.playSound
LOLSDK.stopSound = audio.stopSound
LOLSDK.configureSound = audio.configureSound

function onMesssage(msg) {
  if (msg.data.messageName === 'pause') {
    lifecycleListeners.forEach(function(listener) {
      listener.pauseCallback()
    })
    audio.mute()
  }

  if (msg.data.messageName === 'resume') {
    lifecycleListeners.forEach(function(listener) {
      listener.resumeCallback()
    })
    audio.unmute()
  }

  if (msg.data.messageName === 'questions') {
    questionListeners.forEach(function(cb) {
      const payload = JSON.parse(msg.data.payload)
      cb(payload.questions)
    })
  }
}

// register for messages from LoL platform
window.addEventListener('message', onMesssage)

window.LOLSDK = LOLSDK



